body {
  margin: 20px;
}

section {
  display: flex;
  justify-content: flex-end;
  margin: 0 auto;
  max-width: 1600px;
  position: relative;
}

.details {
  width: 380px;
}

.details p {
  position: fixed;
  top: 0;
}

.pictures {
  display: flex;
  flex-direction: column;
  gap: 20px;
  width: calc(100% - 380px);
}

img {
  width: 100%;
}